/* Panel */
@media (min-width: 1600px) {
  body#jenkins.j-hide-left #main-panel {
    max-width: 75%;
  }
}
@media (min-width: 2000px) {
  body#jenkins.j-hide-left #main-panel {
    max-width: 85%;
  }
}
form {
  margin: 0;
}
input,
textarea,
select {
  color: var(--text-color);
  background-color: var(--input-color);
  font-family: inherit;
}
td {
  vertical-align: top;
}
dt {
  font-weight: bold;
}
.fixed-width {
  font-family: monospace;
}
.center {
  text-align: center;
}
.middle-align td,
td.middle-align {
  vertical-align: middle;
}
.center-align td,
td.center-align {
  text-align: center;
}
.no-wrap td,
td.no-wrap {
  white-space: nowrap;
}
#main-table {
  padding: 0;
  border-collapse: collapse;
}
#top-panel {
  margin-bottom: 3pt;
  height: 34px;
  background: url(../images/topbar.png) repeat-x;
}
#top-panel a {
  text-decoration: none;
}
.skiplink {
  position: absolute;
  left: -20000px;
}
#shutdown-msg {
  font-weight: bold;
  color: white;
  background-color: #ef2929;
  text-align: center;
  margin-left: 2em;
  margin-right: 2em;
  margin-bottom: 0.5em;
  padding: 0.5em;
  -moz-border-radius: 0.5em;
}
