a {
  text-decoration: var(--link-text-decoration);
  font-weight: var(--link-font-weight);
}
a:link {
  color: var(--link-color);
}
a:visited {
  color: var(--link-visited-color);
}
a:hover,
a:focus {
  color: var(--link-color--hover);
  text-decoration: var(--link-text-decoration--hover);
}
a:active {
  color: var(--link-color--active);
  text-decoration: var(--link-text-decoration--active);
}
p a {
  text-decoration: underline;
}
a.lowkey:link {
  text-decoration: none;
  color: inherit;
}
a.lowkey:hover {
  text-decoration: underline;
  color: inherit;
}
a.lowkey:visited {
  text-decoration: none;
  color: inherit;
}
/* tip - anchors of class info */
a.tip {
  position: relative;
  z-index: 24;
  text-decoration: underline;
}
a.tip:hover {
  z-index: 25;
}
a.tip span {
  display: none;
}
a.tip:hover span {
  display: block;
  position: absolute;
  top: 2em;
  left: 2em;
  width: 400px;
  border: 1px solid #bbb;
  background-color: #fffff0;
  color: #000;
  text-align: left;
}
.call-to-action {
  display: table;
  margin-bottom: 5px;
  padding: 10px;
  background-color: var(--alert-info-bg-color);
  border-left: 4px solid #577baa;
  color: var(--call-to-action-text-color);
}
.call-to-action a,
.call-to-action a:visited {
  color: var(--call-to-action-link-color);
  font-weight: bold;
  font-size: larger;
}
img {
  vertical-align: middle;
  border: 0;
}
div.disabled {
  opacity: 0.4;
  filter: alpha(opacity=40);
  /* msie */
  background-color: #000;
}
table.tab {
  border-collapse: collapse;
}
td.selected_tab {
  vertical-align: middle;
  border: 1px #090 solid;
  background: #fff;
}
td.tab_filler {
  background: #fff;
  border-bottom: 1px #090 solid;
}
td.tab {
  vertical-align: middle;
  border: 1px #090 solid;
  background: #f0f0f0;
}
.dashboard td {
  padding: 4px 4px 4px 4px;
}
pre {
  white-space: pre-wrap;
  /* css-3 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */
  margin: 0;
}
pre a {
  word-wrap: break-word;
}
pre.console {
  overflow: auto;
}
.form-group {
  margin-bottom: 1rem;
}
.form-group::after {
  content: " ";
  /* Older browser do not support empty content */
  visibility: hidden;
  display: block;
  height: 0;
  clear: both;
}
.setting-leftspace {
  width: 2em;
}
.setting-name {
  font-weight: 500;
  margin-bottom: 0.5rem;
  white-space: nowrap;
}
.setting-checkbox {
  font-weight: 500;
}
.setting-main {
  margin-top: 0.25rem;
  width: 100%;
  /* try to make this column as big as possible. */
}
.setting-help {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  width: 16px;
  vertical-align: middle;
  float: right;
}
.setting-no-help {
  width: 16px;
}
.setting-input {
  width: 100%;
  border-radius: 3px;
  border: 1px solid var(--input-border);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  padding: 6px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
.setting-description {
  font-size: var(--font-size-xs);
  margin-top: 0;
  padding-top: 0;
}
.setting-name,
.setting-main > input,
.setting-main > textarea {
  vertical-align: middle;
  margin-top: 0;
}
.setting-main > .yui-button {
  clear: both;
  float: right;
}
.expanding-input {
  display: flex;
}
.expanding-input__input {
  flex: 1;
}
.expanding-input__button {
  flex-shrink: 1;
}
.expanding-input__button input {
  min-width: 0 !important;
}
/* div that looks like a hyperlink */
.pseudoLink {
  cursor: pointer;
}
.advancedLink {
  text-align: right;
}
.advancedBody {
  display: none;
}
.scm_info {
  width: 480px;
}
.build-keep {
  font-weight: bold;
}
.task-header {
  display: block;
  border-bottom: 1px #090 solid;
  font-weight: bold;
  font-size: var(--font-size-xs);
}
.jenkins-not-applicable {
  color: darkgrey;
